<a
  class="gio-smart-link"
  class:dark
  class:accent
  rel="noopener"
  href={computedHref}
  {target}
  on:click={handleClick}>
  <slot />
</a>

<script lang="ts">import { createEventDispatcher } from 'svelte';
import { isExternalUrl, toSpaRoute } from '../lib/url';
export let href;
export let disable = false;
export let dark = false;
export let accent = false;
export let spa = false;
const dispatch = createEventDispatcher();
$: isExternal = isExternalUrl(href);
$: computedHref =
    !disable && href ? (spa ? toSpaRoute(href) : href) : undefined;
$: target = !disable && isExternal && href ? '_blank' : undefined;
function handleClick(event) {
    dispatch('click', event);
}
</script>

<style lang="scss">.gio-smart-link {
  display: inline-block;
  cursor: pointer;
}
.gio-smart-link.accent {
  color: #272c35;
  text-decoration: underline;
}
.gio-smart-link.accent.dark {
  color: #f9fafb;
}</style>
